.user {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 10px;
  margin-bottom: 20px;
}

.user_pic {
  display: flex;
  width: 120px;
  height: 120px;
  border-radius: 5px;
  overflow: hidden;
  align-items: center;
  position: relative;
}

.user_pic img {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.user_data {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.user_name {
  font-size: 22px;
  line-height: 22px;
  font-weight: normal;
  margin: 0 0 5px;
}

.user_status {
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
  color: #006699;
  margin-top: 0;
  margin-bottom: 10px;
  width: fit-content;
}

.user_info {
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
  margin: 0;
  width: fit-content;
}

.preloaderContainer {
  width: 100%;
  height:8px;
  overflow: hidden;
  margin-bottom: 5px;
}

.fileUpload input[type="file"] {
  display: none;
}

