  position: absolute;
  bottom: 3px;
  right: 3px;
  font-size: 22px;
  cursor: pointer;
  width: 28px;
  height: 28px;
  background-color: rgba(255, 255, 255, .6);
  border-radius: 3px;
  transition: .3s;
  display: flex;
  align-items: center;
